<template>
  <div class="containerAll">
    <el-alert
      :closable="false"
      :title="'数据一共 ' + total + ' 条'"
      show-icon
      type="info"
    >
    </el-alert>
    <el-table :data="tableDatas" style="width: 100%">
      <el-table-column label="试题编号" prop="number" width="120">
      </el-table-column>
      <el-table-column label="学科" prop="subject" width="120">
      </el-table-column>
      <el-table-column label="目录" prop="catalog" width="120">
      </el-table-column>
      <el-table-column label="题型" width="120">
        <template slot-scope="scope">
          <span v-if="scope.row.questionType === '1'">单选</span>
          <span v-else-if="scope.row.questionType === '2'">多选</span>
          <span v-else>测试</span>
        </template>
      </el-table-column>
      <el-table-column label="提干" width="280">
        <template slot-scope="scope">
          <span v-html="scope.row.question"></span>
        </template>
      </el-table-column>
      <el-table-column label="录入时间" width="180">
        <template slot-scope="scope">
          {{ scope.row.addDate | parseTimeByString('{y}-{m}-{d} {h}:{i}:{s}') }}
        </template>
      </el-table-column>
      <el-table-column label="难度" width="140">
        <template slot-scope="scope">
          <span v-if="scope.row.difficulty === '1'">简单</span>
          <span v-else-if="scope.row.difficulty === '2'">一般</span>
          <span v-else>困难</span>
        </template>
      </el-table-column>
      <el-table-column label="录入人" prop="creator" width="180">
      </el-table-column>
      <el-table-column label="审核状态" prop="chkState" width="180">
        <template slot-scope="scope">
          <span v-if="scope.row.chkState === 0">待审核</span>
          <span v-else-if="scope.row.chkState === 1">已审核</span>
          <span v-else>已拒绝</span>
        </template>
      </el-table-column>
      <el-table-column label="审核意见" prop="chkRemarks" width="180">
      </el-table-column>
      <el-table-column label="审核人" prop="chkUser" width="180">
      </el-table-column>
      <el-table-column label="发布状态" prop="publishState" width="180">
        <template slot-scope="scope">
          <span v-if="scope.row.publishState === 1">已发布</span>
          <span v-else-if="scope.row.publishState === 0">已下架</span>
          <span v-else>待发布</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button size="small" type="text" @click="handleClick(scope.row)"
            >预览</el-button
          >
          <el-button
            :disabled="scope.row.chkState !== 0"
            size="small"
            type="text"
            @click="scopeClick(scope.row.id)"
            >审核
          </el-button>
          <el-button
            :disabled="scope.row.publishState === 1"
            size="small"
            type="text"
            @click="$router.push('/questions/new?id=' + scope.row.id)"
          >
            修改
          </el-button>
          <el-button
            v-if="scope.row.publishState !== 1"
            size="small"
            type="text"
            @click="uptanpe(scope.row)"
            >上架
          </el-button>
          <el-button v-else size="small" type="text" @click="uptanpe(scope.row)"
            >下架
          </el-button>
          <el-button
            :disabled="scope.row.publishState === 1"
            size="small"
            type="text"
            @click="updateById(scope.row)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--    预览-->
    <el-dialog
      :visible.sync="dialogVisible"
      title="题目预览"
      width="900px"
      @close="videos = false"
    >
      <questions-preview
        v-model="videos"
        :dialogData="dialogData"
      ></questions-preview>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"
          >关 闭</el-button
        >
      </span>
    </el-dialog>
    <!--    审核-->
    <questions-check
      v-model="dialogVisibles"
      :choiceCheckId="choiceCheckId"
      @clickday="clickday"
    ></questions-check>
    <el-pagination
      :current-page="questionsSData.page"
      :page-size="questionsSData.pagesize"
      :page-sizes="[5, 10, 20, 40]"
      :total="total"
      background
      layout=" prev, pager, next,sizes, jumper"
      style="float: right;margin-bottom: 20px"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </div>
</template>

<script>
import { choice, choicePublish, remove } from '@/api/hmmm/questions'
import questionsPreview from '@/module-hmmm/components/questions-preview'
import QuestionsCheck from '@/module-hmmm/components/questions-check'

export default {
  name: 'containerAll',
  props: ['questionsSData', 'total', 'tableData'],
  data () {
    return {
      videos: false,
      dialogData: [],
      dialogVisible: false,
      dialogVisibles: false,
      choiceCheckId: null,
      tableDatas: []
    }
  },
  watch: {
    tableData (oldValue) {
      this.tableDatas = oldValue
    }
  },
  created () {
    this.getChoice()
  },
  methods: {
    async getChoice () {
      const { data } = await choice(this.questionsSData)
      // console.log(data)
      this.tableDatas = data.items
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
      this.questionsSData.pagesize = val
      this.getChoice()
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.questionsSData.page = val
      this.getChoice()
    },
    handleClick (obj) {
      this.dialogVisible = true
      this.dialogData = obj
      // console.log(obj)
    },
    scopeClick (id) {
      this.dialogVisibles = true
      this.choiceCheckId = id
    },
    uptanpe (obj) {
      // console.log(obj)
      this.$confirm(
        obj.publishState !== 1
          ? '您确认上架这道题目吗?'
          : '您确认下架这道题目吗?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).then(async () => {
        if (obj.publishState !== 1) {
          obj.publishState = 1
        } else {
          obj.publishState = 0
        }
        const data = await choicePublish(obj)
        console.log(data)
        this.getChoice()
        if (data.status === 200) {
          this.$message({
            type: 'success',
            message: obj.publishState !== 1 ? '上架成功!' : '下架成功'
          })
        }
      })
    },
    updateById (obj) {
      this.$confirm('此操作将永久删除该题目, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const data = await remove(obj)
        console.log(data)
        if (data.status === 200) {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        }
      })
    },
    clickday () {
      this.getChoice()
    }
  },
  components: {
    QuestionsCheck,
    questionsPreview
  }
}
</script>

<style lang="scss" scoped>
.containerAll {
  .el-table {
    margin: 20px 0;
  }

  ::v-deep.el-dialog__footer {
    text-align: right;
  }
}
</style>
