<template>
  <el-dialog
    :visible.sync="dialogVisibles"
    title="题目审核"
    width="400px">
    <el-radio-group v-model="radio">
      <el-radio :label="1">通过</el-radio>
      <el-radio :label="2">拒绝</el-radio>
    </el-radio-group>
    <el-input
      v-model="textarea"
      :autosize="{ minRows: 2, maxRows: 4}"
      placeholder="请输入审核内容"
      type="textarea">
    </el-input>
    <span slot="footer" class="dialog-footer">
    <el-button @click="$emit('input', !dialogVisibles)">取 消</el-button>
    <el-button type="primary" @click="clickday">确 定</el-button>
  </span>
  </el-dialog>
</template>

<script>
import { choiceCheck } from '@/api/hmmm/questions'

export default {
  props: ['dialogVisibles', 'choiceCheckId'],
  model: {
    prop: 'dialogVisibles'
  },
  data () {
    return {
      radio: 1,
      textarea: ''
    }
  },
  methods: {
    async clickday () {
      const data = await choiceCheck({
        chkState: this.radio,
        chkRemarks: this.textarea,
        id: this.choiceCheckId
      })
      console.log(data)
      if (data.status === 200) {
        this.$message.success('操作成功')
        this.$emit('input', !this.dialogVisibles)
        this.textarea = ''
        this.$emit('clickday')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.el-radio-group {
  margin-bottom: 20px;
}

.el-textarea {
  margin-bottom: 10px;
}
</style>
