<template>
  <div class="container">
    <el-card class="box-card">
      <questions-search
        :questionsSData="questionsSData"
        @submitForm="submitForm"
      ></questions-search>
      <!--      <div >111</div>-->
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="全部" name="-1">
          <container-all
            :questionsSData="questionsSData"
            :tableData="tableData"
            :total="total"
          ></container-all>
        </el-tab-pane>
        <el-tab-pane label="待审核" name="0">
          <container-all
            :questionsSData="questionsSData"
            :tableData="tableData"
            :total="total"
          ></container-all>
        </el-tab-pane>
        <el-tab-pane label="已审核" name="1">
          <container-all
            :questionsSData="questionsSData"
            :tableData="tableData"
            :total="total"
          ></container-all>
        </el-tab-pane>
        <el-tab-pane label="已拒绝" name="2">
          <container-all
            :questionsSData="questionsSData"
            :tableData="tableData"
            :total="total"
          ></container-all>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import questionsSearch from '@/components/questionsSearch'
import containerAll from '@/components/container/containerAll'
import { choice } from '@/api/hmmm/questions'

export default {
  data () {
    return {
      questionsSData: {
        subjectID: null, // 学科
        difficulty: null, // 难度
        questionType: null, // 试题类型
        tags: null, // 标签名称
        province: null, // 企业所在地省份
        city: null, // 企业所在城市
        keyword: null, // 关键字
        remarks: null, // 题目备注
        shortName: null, // 企业简称
        direction: null, // 方向
        creatorID: null, // 录入人
        catalogID: null, // 目录
        chkState: null // 审核状态
      },
      pages: {
        page: 1, // 当前页数
        pagesize: 5 // 页尺寸
      },
      activeName: '-1',
      tableData: [],
      total: 0
    }
  },
  created () {
    this.getChoice()
  },
  methods: {
    async getChoice () {
      const obj = { ...this.pages, ...this.questionsSData }
      const { data } = await choice(obj)
      console.log(data)
      this.total = data.counts
      this.tableData = data.items
    },
    handleClick (tab) {
      // console.log(tab.name, event)
      if (tab.name !== '-1') {
        this.questionsSData.chkState = tab.name
        this.getChoice()
      } else {
        this.questionsSData.chkState = null
        this.getChoice()
      }
    },
    // 搜索
    submitForm (obj) {
      this.questionsSData = obj
      this.getChoice()
    }
  },
  components: {
    questionsSearch,
    containerAll
  }
}
</script>

<style lang="scss" scoped>
.container {
  height: 100%;

  .box-card {
    margin: 10px;

    .questions-search {
      margin-bottom: 20px;
    }
  }
}
</style>
