<template>
  <div class="qsearch">
    <div class="btn_wrapper">
      <span>说明：目前支持学科和关键字条件筛选</span>
      <el-button
        icon="el-icon-edit"
        size="small"
        type="success"
        @click="$router.push('/questions/new')"
        >新增试题
      </el-button>
    </div>
    <el-form
      ref="questionsSData"
      :model="questionsSData"
      height="150px"
      label-width="80px"
    >
      <el-form-item class="el-cord6" label="学科">
        <el-select
          v-model="questionsSData.subjectID"
          placeholder="请选择"
          @change="subId(questionsSData.subjectID)"
        >
          <el-option
            v-for="item in subSimpData"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" label="二级目录">
        <el-select v-model="questionsSData.catalogID" placeholder="请选择">
          <el-option
            v-for="item in dirSimpData"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" label="标签">
        <el-select v-model="questionsSData.tags" placeholder="请选择">
          <el-option
            v-for="item in tagsSimpData"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" label="关键字">
        <el-input
          v-model="questionsSData.keyword"
          placeholder="根据题干搜索"
        ></el-input>
      </el-form-item>
      <el-form-item class="el-cord6" label="试题类型">
        <el-select v-model="questionsSData.questionType" placeholder="请选择">
          <el-option
            v-for="item in questionType"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" label="难度">
        <el-select v-model="questionsSData.difficulty" placeholder="请选择">
          <el-option
            v-for="item in difficulty"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" label="方向">
        <el-select v-model="questionsSData.direction" placeholder="请选择">
          <el-option
            v-for="(item, index) in direction"
            :key="index"
            :label="item"
            :value="index"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" label="录入人">
        <el-select v-model="questionsSData.creatorID" placeholder="请选择">
          <el-option
            v-for="item in userSimData"
            :key="item.id"
            :label="item.username"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" label="题目备注">
        <el-input
          v-model="questionsSData.remarks"
          placeholder="关键字"
        ></el-input>
      </el-form-item>
      <el-form-item class="el-cord6" label="企业简称">
        <el-input
          v-model="questionsSData.shortName"
          placeholder="关键字"
        ></el-input>
      </el-form-item>
      <el-form-item class="el-cord6" label="城市">
        <el-select
          v-model="questionsSData.province"
          placeholder="请选择"
          style="width: 47%;"
          @change="cityChange(questionsSData.province)"
        >
          <el-option
            v-for="(item, index) in provincesData"
            :key="index"
            :label="item"
            :value="index"
          ></el-option>
        </el-select>
        <el-select
          v-model="questionsSData.city"
          placeholder="请选择"
          style="width: 47%; margin-left: 6%;"
        >
          <el-option
            v-for="(item, index) in citysData"
            :key="index"
            :label="item"
            :value="index"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="el-cord6" style="text-align: right;">
        <el-button @click="resetForm(questionsSData)">清除</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >搜索</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { simple as subSimple } from '@/api/hmmm/subjects'
import { simple as dirSimple } from '@/api/hmmm/directorys'
import { simple as tagsSimple } from '@/api/hmmm/tags'
import { citys, provinces } from '@/api/hmmm/citys'
import { difficulty, direction, questionType } from '@/api/hmmm/constants'
import { simple as usersSimple } from '@/api/base/users'

export default {
  name: 'index.vue',
  props: ['questionsSData'],
  data () {
    return {
      subSimpData: [],
      dirSimpData: [],
      tagsSimpData: [],
      provincesData: provinces(),
      citysData: [],
      userSimData: [],
      difficulty: difficulty,
      questionType: questionType,
      direction: direction
    }
  },
  created () {
    this.getQuest()
  },
  methods: {
    // 获取学科简单列表和用户简单列表
    async getQuest () {
      const { data } = await subSimple()
      const { data: data1 } = await usersSimple()
      this.subSimpData = data
      console.log(data1)
      this.userSimData = data1
    },
    // 获取标签简单列表和目录简单列表
    async subId (id) {
      console.log(id)
      const { data } = await dirSimple({ subjectID: id })
      const { data: data1 } = await tagsSimple({ subjectID: id })
      console.log(data1)
      console.log(data)
      this.dirSimpData = data
      this.tagsSimpData = data1
    },
    // 获取城市下地区
    cityChange (i) {
      this.citysData = citys(this.provincesData[i])
      console.log(this.citysData)
    },
    // 清除表单数据
    resetForm () {
      Object.keys(this.questionsSData).forEach(item => {
        this.questionsSData[item] = null
      })
      console.log(this.questionsSData)
      this.$emit('resetForm', this.questionsSData)
    },
    // 搜索
    submitForm (formName) {
      console.log(formName)
      console.log(this.questionsSData)
      this.$emit('submitForm', this.questionsSData)
    }
  }
}
</script>

<style lang="scss" scoped>
.qsearch {
  height: 280px;

  .btn_wrapper {
    height: 30px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;

    span {
      font-size: 12px;
      color: pink;
    }
  }

  .el-cord6 {
    float: left;
    width: 25%;
    .el-select {
      width: 100%;
    }
  }
}
</style>
